import styles from './nickname.module.css';
import { useState } from 'react';
import { Link } from 'react-router-dom';



function App(){
    const [UserNickname,setUserNickname] = useState('');
    const [Isnickname, setIsnickname] = useState(false);
    const saveNickname = event =>{
        setUserNickname(event.target.value)
        setIsnickname(event.target.value.toLowerCase().match(/^[-|-|a-z|A-Z|0-9|].{1,7}$/))
    }
    return(
        <div className={styles.background4}>
            <div className={styles.signin1}>
                <Link to="/password"><div className={styles.arrow4}></div></Link>
                <div className={styles.pagetitle1}></div>
            </div>
            <div className={styles.nicknametitle}> <br/>  </div>
            <div className={styles.circle}><div className={styles.camera}></div></div>
            <div className={styles.nick}>
                <input className={styles.nickname} onChange={saveNickname} type='text' placeholder=' ' minLength={1} maxLengthlength="8" required></input>
            </div>
            
            
            <div className={styles.buttonarea2}>
                
                {
                    Isnickname ? <Link to="/page3" ><button type='submit'  value="" className={styles.button4} ></button></Link>
                    :<div ><button type='submit' className={styles.button5} value=""></button></div>
                }
            </div>
        </div> 
    );
}   
export default App;