import styles from './loginpage.module.css';
import React, {useCallback, useState, useEffect } from 'react';
import { Link } from 'react-router-dom';




function App(){
   
    const [IsEmail, setIsEmail]=useState(false);
    const [UserEmail, setUserEmail]=useState("");
    const saveEmail = event =>{
        setUserEmail(event.target.value); //   useremail  
        console.log(event.target.value);
        setIsEmail(event.target.value.toLowerCase().match(/([\w-.]+)@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.)|(([\w-]+\.)+))([a-zA-Z]{2,4}|[0-9]{1,3})(\]?)$/));
    }
    
    return(
        <div className={styles.background2}>
            <Link to="/photoalbum"><div className={styles.close}></div></Link>
            <div className={styles.logintitle}>   <br/>  ?</div>
            <div className={styles.emailform} style={ IsEmail ? {borderBottom: '0.1vh solid #62E857'} : {borderBottom: '0.1vh solid red'} }>
                <div className={styles.emailtitle}>  .</div>
                <input type="email" onChange={saveEmail} placeholder='email@example.com' className={styles.email}></input>
            </div>
            
            {
                IsEmail ? <div className={styles.emailc}>  . </div> : <div className={styles.emailcr}>  .</div>
            }
            <div className={styles.buttonarea}>
                {
                    IsEmail ? <Link to="/password" className={styles.buttonarea}><button type='submit' className={styles.button} value=""></button></Link>
                    : <div className={styles.move1}><button type='submit' className={styles.button1} value=""></button></div>
                }
                
            </div>
        </div>
    );
}   
export default App;