import styles from "./css/PhotoAlbumModal.module.css";
import React, { useState, useRef, useEffect } from "react";
import { render } from "react-dom";

// App.defaultProps = {
//   userList: [],
// };

/*
  what to do?
     
  userId    
*/

const App = (props) => {

  const titleInputRef = useRef(null); // DOM    useRef 
  const idInputRef = useRef(null);

  const [pageIdx, setPageIdx] = useState(1); // page    (1 ~ 2)
  const [albumTitle, setalbumTitle] = useState(''); // input  albumTitle 
  const [userID, setUserID] = useState(''); // input  userID 
  const [userList, setUserList] = useState([]);
  const [userListFilter, setUserListFilter] = useState([]);
  // const [getUserList, setGetUserList] = useState([
  //   {id:'jayou'}, {id:'soyoung'}, {id:'juyeon'}, {id:'yeonghyeon'}, 
  //   {id:'chaeeun'}, {id:'hyojeong'}
  // ]);
  const [getUserList, setGetUserList] = useState([
    'jayou', 'soyoung', 'juyeon', 'yeonghyeon', 
    'chaeeun', 'hyojeong'
  ]);


// ========== title input focus ==========

  const titleInputFocus = () => {
    titleInputRef.current.focus();
  };

// ========== userId input focus ==========

  const idInputFocus = () => {
    idInputRef.current.focus();
  };

  useEffect(() => {
    if (titleInputRef.current) {
      titleInputFocus();
    }
    if(idInputRef.current){
      idInputFocus();
    }
  }, [pageIdx]); // pageIdx   (, /   ) input focus

  const changePageIdx = (idx) => { // page   pageIdx  
    setPageIdx(idx);
    setUserList([]);
    setUserListFilter([]);
  }
  
// ===== input value handle =====
  const saveUserID = event => { //userId     userId  
    setUserID(event.target.value);
    setUserListFilter(
      event.target.value=='' ? [] : getUserList.filter((item) => item.includes(event.target.value))
      );
    console.log(userListFilter);
  }

  const saveAlbumTitle = event => {
    setalbumTitle(event.target.value);
  }



  const onCancel = () => {
    props.modalState();
   }

  const goToPage2 = () => { //       
    setPageIdx(2); // pageIdx 2 
    
  }

  // userId   
  const pushBackToUserList = (value) => {
    if(userList.find(num => num == value) != null){
      console.log("  user.");
    } else {
      console.log("");
      setUserList([...userList, value]);
      setUserID('');
      idInputFocus();
      const result = userID != '' ? getUserList.filter((item) => item.includes(userID)) : [];
      setUserListFilter(result);
      console.log(userListFilter);
    }
  }


  if(pageIdx == 1){
    return(
      <div className={styles.Modal}>
        <div className={styles.ModalBackground}></div>
        <div className={styles.inviteAria}>
          <div className={styles.nav}>
            <div onClick={onCancel}></div>
            <div style={{fontSize:'2.2vh'}}> </div>
            <NextBtn value={albumTitle} goToPage2={goToPage2}/>
          </div>
          <div>
            <input ref={titleInputRef} onChange={saveAlbumTitle} value={albumTitle} type="text" placeholder="  ( 12)" maxLength={10} minLength={1} className={styles.inputAlbumName}></input>
          </div>
          <div className={styles.example}>: , </div>
        </div>
      </div>
    )
  } else {
    return (
      <div className={styles.Modal}>
          <div className={styles.ModalBackground}></div>
          <div className={styles.inviteAria}>
            <div className={styles.nav}>
            <div onClick={() => changePageIdx(1)}></div>
            <div onClick={() => idInputFocus()} style={{fontSize:'2.2vh'}}></div>
            <GenarateBtn value={userID} pushBackToUserList={pushBackToUserList}/>
            
          </div>
          <div className={styles.inputUserIdArea}>
              : &nbsp;
            {userList.map((userList, index) => <span key={index} style={{color:"black"}}>{userList}&nbsp;</span>)}
            <input ref={idInputRef} onChange={saveUserID} value={userID} type="text" maxLength={12} className={styles.inputUserId}></input>
          </div>
          <ul className={styles.filterUsers}>
            {userListFilter.map((userListFilter, index) => <li key={index} onClick={() => pushBackToUserList(userListFilter)}>{userListFilter}&nbsp;</li>)}
          </ul>
        </div>
      </div>
      
    )
  }
}

function NextBtn(props) { // input       ''  return
  if(props.value.length > 0 && props.value.length < 11){ // album title    (,   )
    return <div style={{color:'#113C7E'}} onClick={()=>{props.goToPage2()}}></div> // 
  } else {
    return <div style={{color:'#B7B6B4'}}></div> // 
  }
}

function GenarateBtn(props) { // input       ''  return
  const {pushBackToUserList} = props;
  if(props.value.length > 1 && props.value.length < 13){ // userId    
    return <div style={{color:'#113C7E'}} onClick={() => pushBackToUserList(props.value)} ></div> // 
  } else {
    return <div style={{color:'#B7B6B4'}}></div> // 
  }
}

export default App;
