import styles from "./css/MainPage.module.css";
import stylesTap1 from "./css/MainPageTap1.module.css";
import stylesTap2 from "./css/MainPageTap2.module.css";
import styled from "styled-components";
import {
  React,
  useState,
  useEffect,
} from "react";
import Wrapper from './recircleComponets/Wrapper';
import InfiniteScroll from "../infiniteScroll/InfiniteScroll"
import { Link } from 'react-router-dom';
import PhotoAlbumModal from "./PhotoAlbumModal";


const TabMenu = styled.ul`
  @import url("./font.css");
  
  color: white;
  font-weight: bold;
  display: flex;
  flex-direction: row;
  align-items: center;
  list-style: none;
  width: 100%;

  .submenu {
    //  Tabmenu   CSS 

    text-align: center;
    width: calc(100% / 2);
    padding: 2vh;
    font-size: 1.7vh;
    transition: 3s all ease;
    border-bottom: 0.1vh solid white;
    font-size: 2.2vh;
    font-family: 'Pretendard-ExtraLight';
  }

  .focused {
    // Tabmenu   CSS 
  }

`;

const MainPage = (props) => {
  const [currentTab, clickTab] = useState(0);

  const menuArr = [
    { name: "", content: <Tap1 currentTab={currentTab} api={props.api}/> },
    { name: "", content: <Tap2 currentTab={currentTab} api={props.api}/> },
  ];

  const selectMenuHandler = (index) => {
    // parameter      ,  (event)  
    //      Tab Menu  .
    clickTab(index);
  };

  return (
    <div className={styles.App}>
        <div className={styles.topNav}>
          <div className={styles.logo}></div>
          <div className={styles.userIcon}></div>
        </div>
        <TabMenu>
          {/*   , map    
            li  class   tab  'submenu focused',  2 tab 'submenu' 
            <li className="submenu">{menuArr[0].name}</li>
            <li className="submenu">{menuArr[1].name}</li>
            <li className="submenu">{menuArr[2].name}</li> */}
          {menuArr.map((el, index) => (
            <div key={index} className={index === currentTab ? "submenu focused" : "submenu"}
              onClick={() => selectMenuHandler(index)}>
                {el.name}
            </div>
          ))}
        </TabMenu>
        <div className={currentTab == 0 ? styles.borderL : styles.borderR}></div>
      {menuArr[currentTab].content}
    </div>
  );
};

export default MainPage;

const Tap1 = (props) => {
  const [Tap1headerComponet, setTap1headerComponet] = useState(
    <div className={stylesTap1.tap1Header}>
      <div className={stylesTap1.tap1HeaderTotal}> 00</div>
      <div className={stylesTap1.tap1HeaderPeople}></div>
    </div>
  );
  
  return(
    <div className={stylesTap1.Tap1}>
      <div className={stylesTap1.Banner}>
        <div>
          <div className={stylesTap1.BannerDiv1}>    ?</div>
          <div className={stylesTap1.BannerDiv2}></div>
        </div>
        <div className={stylesTap1.BannerImg}></div>
      </div>
      <InfiniteScroll api={props.api} heightOfComponet={'66vh'} headerComponet={Tap1headerComponet}/>
    </div>
  )
}

const Tap2 = (props) => {
  const [Tap2headerComponet, setTap2headerComponet] = useState(
    <div className={stylesTap2.tap2Header}>
      <div className={stylesTap2.tap2HeaderTitle}>juyeon </div>
    </div>
  );

  const [modal, setModal] = useState(false);

  const modalState = () => {
    setModal(!modal);
  }

  return(
    <div className={styles.Tap2}>
      { modal == true ? <PhotoAlbumModal modalState={modalState} /> : null } {/* modal  */}
      <InfiniteScroll api={props.api} heightOfComponet={'81vh'} headerComponet={Tap2headerComponet}/>
      <div className={stylesTap2.createAlbum} onClick={()=>{setModal(!modal)}}></div>
    </div>
  )
}
