import styles from './password.module.css';
import React, {useCallback, useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import axios from "axios";



function App(){
    const [UserPassword,setUserPassword]=useState("");
    const [IsPassword, setIsPassword] = useState(false);

    const savePassword = event =>{
        setUserPassword(event.target.value);
        console.log(event.target.value);
        setIsPassword(event.target.value.toLowerCase().match(/^(?=.*[a-zA-Z])(?=.*[!@#$%^*+=-])(?=.*[0-9]).{10,25}$/))
    }

    const [UserPwc, setUserPwc]=useState("");
    const [IsPwc, setIsPwc]=useState(false);

    const PasswordC=event =>{
        setUserPwc(event.target.value);
        console.log(event.target.value);
        setIsPwc(event.target.value==UserPassword);
    }
    
    return(
        <div className={styles.background3}>
            <div className={styles.signin}>
                <Link to="/loginpage"><div className={styles.arrow3}></div></Link>
                <div className={styles.pagetitle}></div>
            </div>
            <div className={styles.pass} style={ IsPassword ? {borderBottom: '0.1vh solid #62E857'} : {borderBottom: '0.1vh solid red'}}>
                <div className={styles.pass1}> </div>
                <input className={styles.password}type='password' onChange={savePassword} placeholder=' ( , ,  8~16)' minlength="8" required></input>
                <p className={styles.passerror} style={IsPassword ? {color:'#1C54A9'} : {color:'red'}}> , ,  8~16 .</p>
            </div>
            
            <div className={styles.pass2} style={ IsPwc ? {borderBottom: '0.1vh solid #62E857'} : {borderBottom: '0.1vh solid red'}}>
                <div className={styles.pass3}> </div>
                <input className={styles.passwordc}type='password' onChange={PasswordC} placeholder='  ' minlength="8" required></input>
                <p className={styles.passerror1} style={IsPwc ? {color:'#1C54A9'}: {color:'red'}}>  .</p>
            </div>
            
            <div className={styles.buttonarea1}>
                
                {
                    IsPwc ? <Link to="/nickname" className={styles.buttonarea1}><button type='submit'  value="" className={styles.button2}></button></Link>
                    : <div className={styles.move2}><button type='submit' className={styles.button3} value=""></button></div>
                }
            </div>
        </div> 
    );
}   

export default App;