import './css/Inflow4.css';
import { useState } from 'react';
import { useNavigate } from 'react-router-dom'


function AppPage4(){

  const navigate = useNavigate();

  const [isActive, setIsActive] = useState(false);
  const [selectedOption, setSelectedOption] = useState('');
  
  const options = ['solo', '2', '3', '4','5','6',' '];
  
  const handleSelect = (item) => {
    setSelectedOption(item);
    setIsActive(false);
  };

  const toggleDropdown = () => {
    setIsActive(!isActive);
    console.log('isActive:', isActive);
  };
  
  

  return (
    <div className="back1">
      <div className="tag3">
        <div className="arrow" onClick={() => navigate(-1)}></div>
        <h3 className="tag4"> </h3>
      </div>
      <div className="circle">     
        <button className ="label" onClick={() => toggleDropdown()}><svg className="icon1" stroke="currentColor" fill="currentColor" stroke-width="0" viewBox="0 0 16 16" height="1.5vh" width="1.5vh" xmlns="http://www.w3.org/2000/svg"><path fill-rule="evenodd" d="M7.022 1.566a1.13 1.13 0 011.96 0l6.857 11.667c.457.778-.092 1.767-.98 1.767H1.144c-.889 0-1.437-.99-.98-1.767L7.022 1.566z" clip-rule="evenodd"></path></svg></button>
        <ul className="submenu">
          <li><a className="optionItem"></a></li>
          <li><a className="optionItem">2</a></li>
          <li><a className="optionItem">3</a></li>
          <li><a className="optionItem">4</a></li>
          <li><a className="optionItem">5</a></li>
          <li><a className="optionItem">6</a></li>
          <li><a className="optionItem"> </a></li>
        </ul>
          
      </div>   

      <div className="block">
        <div className="block1"></div>
        <div className="block2"></div>
        <div className="block3"></div>
        <div className="block4"></div>
        <div className="block5"></div>
        <div className="block6"></div>
        <div className="block7"></div>
        <div className="block8"></div>
        <div className="block9"></div>
        <div className="block10"></div>
      </div>
    </div>    
  )
}
  

export default AppPage4;






